import React, { Component } from 'react';
import './App.css';
import Todo from './todo';
import NewTodo from './NewTodo';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: ["poopy", "peepee"],
    };
  }
  renderTodo(i){
    return <Todo value={this.state.list[i]} />;
  }
  handleSubmit(){
    alert("submitted");
    // const list = this.state.list.slice();
    // this.setState(list.push())
  }
  render() {
    return (
      <div id="body">
            <div id="header">
                <h1>Quarantine ToDo list</h1>
            </div>
            <div id="addthing">
                <NewTodo onSubmit={() => this.handleSubmit()}/>
            </div>
            
            <div>
            <ul id = "todoslist">
                {this.renderTodo(0)}
                {this.renderTodo(1)}
            </ul>
        </div>
        </div>
    );
  }
}

export default App;